import { ZIndex } from '@tsparticles/engine';
import React, { useEffect, useState } from 'react';

interface TypewriterEffectProps {
    text: string;
    typingSpeed?: number;
}

const TypewriterGreeting: React.FC<TypewriterEffectProps> = ({ text, typingSpeed = 100 }) => {
    const [displayedText, setDisplayedText] = useState<string>('');

    useEffect(() => {
        let index = 0;
        const timer = setInterval(() => {
            if (index < text.length) {
                setDisplayedText((prev) => prev + text.charAt(index));
                index++;
            } else {
                clearInterval(timer);
            }
        }, typingSpeed);

        return () => clearInterval(timer);
    }, [text, typingSpeed]);

    return (
        <div style={{zIndex:99,backgroundColor:"#222"}} className="typewriter-container">
            <div className="typewriter-text">{displayedText}</div>
        </div>
    );
};

export default TypewriterGreeting;
